








































export default {
	data() {
		const currentDate = this.getDate({
			format: true
		});
		return {
			index: 0,
			array: ['男', '女'],
			date: currentDate,
			user_data: '',
			headerImageSrc: 'http://img3.imgtn.bdimg.com/it/u=466871861,443615080&fm=26&gp=0.jpg',
			util_list: [{
				name: '昵称',
				info: '编辑昵称',
				target: 'nickname',
			}, {
				name: '性别',
				info: '未设置',
				target: 'sex',
			}, {
				name: '出生日期',
				info: '未设置',
				target: 'birthday',
			}, {
				name: '身高',
				info: '未设置',
				target: 'height',
			}, {
				name: '手机号码',
				info: '未设置',
				target: 'phone',
			}, {
				name: '体重',
				info: '未设置',
				target: 'weight',
			}]
		};
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onUnload() {
		this.headerImageSrc = '';
	},
	onShow: function() {
		let user_data = this.$store.state.userData;
		if (Array.isArray(user_data)) {
			this.setListData(user_data[0], user_data[1]);
		}
	},
	methods: {
		setListData(key, value) {
			this.util_list.forEach(function(item, index) {
				if (item.target == key) {
					item.info = value;
				}
			});
		},
		bindPickerChange(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value;
		},
		bindDateChange(e) {
			this.date = e.target.value;
		},
		openTag(item) {
			if (item.target != 'sex' && item.target != 'birthday') {
				this.open(item.target);
			}
		},
		open(type) { //打开
			switch (type) {
				case 'nickname':
					this.target('datachecker', type, 'template');
					break;
				case 'sex':
					this.target('mpvuePicker', type, 'components');
					break;
				case 'height':
					this.target('datachecker', type, 'template');
					break;
				case 'weight':
					this.target('datachecker', type, 'template');
					break;
				case 'phone':
					this.target('datachecker', type, 'template');
					break;
				default:
					break;
			}
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;

			return `${year}-${month}-${day}`;
		},
		target(tag, type, base) {
			uni.navigateTo({
				url: '../../' + base + '/' + tag + '/' + tag + '?type=' + type,
				success: res => {
					console.log(res)
				},
				fail: (res) => {
					console.log(res)
				}
			});
		},
		chooseImage: function() {
			uni.chooseImage({
				count: 1, //图片数量
				sizeType: ['compressed'], //原图，缩略图['original', 'compressed']
				sourceType: ['album'], //否 album 从相册选图，camera 使用相机，默认二者都有
				success: (res) => {
					console.log('chooseImage success, temp path is', res.tempFilePaths[0])
					var imageSrc = res.tempFilePaths[0]
					uni.uploadFile({
						url: "https://www.icoo.tech/common/sysFile/testuploadimg",
						filePath: imageSrc,
						name: 'file',
						success: (res) => {
							console.log('uploadImage success, res is:', res)
							uni.showToast({
								title: '上传成功',
								icon: 'success',
								duration: 1000
							})
							this.headerImageSrc = imageSrc
						},
						fail: (err) => {
							console.log('uploadImage fail', err);
							uni.showModal({
								content: err.errMsg,
								showCancel: false
							})
						},
						complete: () => {
							console.log("complate")
						}
					})

				},
				fail: (err) => {
					console.log('chooseImage fail', err)
				}
			})
		}
	},
}



